.build-a-spore.nft-jacker {
    .mint-button {
        color: white;
        border-radius: 15px;
        cursor: not-allowed;
        position: absolute;
        left: 0;
        text-align: center;
        width: 120px;
        background-color: #817e7e;
        opacity: 0.3;
        right: 0;
        margin-left: auto;
        margin-right: auto;
        bottom: 100px;
        padding: 5px;
    }

    .center-builder {
        margin: auto;
        display: flex;


        .image-handler {
            width: 200px;
            height: 200px;
            display: flex;
            margin-right: 30px;
            border: 1px solid white;

            .drop-here {
                display: flex;
                width: 100%;
                height: 100%;

                .inner {
                    width: 100%;
                    height: 100%;

                    img {
                        width: 100%;
                        height: 100%;
                    }
                }

                span {
                    margin: auto;
                    text-align: center;
                }
            }
        }
    }
    iframe {
        opacity: unset;
        pointer-events:  unset;
        filter: unset;
        margin-right: 30px;
    }
}
